const fetchTickers = async function (
	marketList: any[],
	windowSize: string = '1d'
) {
	const marketsStore = useMarketsStore();
	let symbolsTickerList = [] as any[];
	const marketSymbols = marketList.map((obj: any) => obj.symbol);
	while (marketSymbols.length) {
		// const tickerData = await exchange['public_get_ticker']({
		// 	symbols: JSON.stringify(marketSymbols.splice(0, 100)),
		// 	windowSize: '1d',
		// });
		let tickerData = [] as any;
		try {
			const tickerResponse = (tickerData = await fetch(
				`https://api.binance.com/api/v3/ticker?symbols=${encodeURIComponent(
					JSON.stringify(marketSymbols.splice(0, 100))
				)}&windowSize=${windowSize}`
			));
			tickerData = await tickerResponse.json();
		} catch (error) {}
		symbolsTickerList = symbolsTickerList.concat(tickerData);
	}
	return symbolsTickerList;
};
