import { defineStore } from 'pinia';

export const useAppStore = defineStore({
	id: 'app',
	state: () => ({
		menuItems: [] as MenuItem[],
	}),
	getters: {},
	actions: {
		// 为了可以做权限管理
		setupMenuItems() {
			this.menuItems = [
				{
					key: '1',
					name: '市场监控',
					icon: 'RadarChartOutlined',
				},
				{
					key: '2',
					name: 'coming more',
					icon: null,
				},
			];
		},
	},
});

interface MenuItem {
	key: string;
	icon: string | null;
	name: string;
}
